@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400&display=swap);
*{font-family:'Roboto', sans-serif}


.layout{max-width:1300px;margin:0 auto;padding:0}.content{margin-top:4vw;padding:0 0.5vw 0 0.5vw;display:grid;grid-template-columns:2.8fr 1.2fr;grid-column-gap:0.5vw}

nav{width:60%;text-align:center;min-height:5vw}nav ul{list-style:none;margin:0;padding:0;min-height:5vw;display:grid;grid-template-columns:repeat(4, 1fr)}nav ul li{display:inline-block;-webkit-align-self:center;align-self:center;transition:all .2s ease-in}nav ul li a{text-decoration:none;font-size:1vw;color:#223046}nav ul li a:hover{color:black}

.headerWrap{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly}.header{min-height:5vw}.header hr{margin:0;border:1px solid #223046}.logo{margin-left:-24px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.logo a{text-decoration:none}.logo h2{font-family:'Lato', sans-serif;color:#223046;font-size:1.6vw;cursor:pointer}.logo h2 span{opacity:0;color:green;transition:opacity .3s ease-in}.logo h2:hover>span{opacity:1}.person{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.person i{color:#223046;font-size:1.6vw;cursor:pointer;transition:all .3s ease-in}.person i:hover{color:black}

.exchange{min-height:16vw;border:1px solid #BFC4CD;padding:1vw 0 1vw 0}.exchangeContainer{width:90%;margin:0 auto}.exchangeHead{font-weight:bold;padding:1vw}.exchangeContent ul{list-style:none;margin:0;padding:0}.exchangeContent ul li{padding-left:25%;border-bottom:1px solid #BFC4CD;margin:.5vw auto .5vw auto;display:grid;grid-template-columns:1fr 1fr}.exchangeContent ul li img{width:20px;width:15px}


.sidebar{border:1px solid #BFC4CD}.sidebarHead{border-bottom:1ps solid #BFC4CD;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.sidebarContent ul{list-style:none;margin:0;padding:0}.sidebarContent ul li{padding-left:25%;border-bottom:1px solid #BFC4CD;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start}.sidebarContent ul li img{width:20px;height:15px}

.btn{font-size:1vw;color:#223846;border-radius:5px;border:1px solid #BFC4CD;cursor:pointer}.btn:hover{color:#fff;background-color:#223846}.btn:disabled{cursor:unset}.btn:disabled:hover{color:#223846;background-color:#C9DDF2}

.operation{display:grid;grid-template-columns:2fr 1fr 1fr;grid-column-gap:1vw}

.calcResult{margin-top:2vw}.calcResult ul{list-style:none;margin:0;padding:0}.calcResult ul li{padding-left:25%;border-bottom:1px solid #BFC4CD;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start}.calcResult ul li img{width:20px;height:15px}

.calculator{min-height:16vw;border:1px solid #BFC4CD}.calcContainer{width:90%;margin:0 auto;padding:1vw 0 1vw 0}

.sample{min-height:16vw;border:1px solid #BFC4CD}.sampleContainer{width:90%;margin:0 auto;padding:1vw 0 1vw 0}.sampleHead{width:70%;display:grid;grid-template-columns:2fr 1fr}.sampleResult{margin-top:2vw;margin-bottom:2vw}.sampleResult ul{list-style:none;margin:0;padding:0}.sampleResult ul li{padding-left:25%;border:1px solid #BFC4CD;display:block;margin-bottom:1vw;padding:.5vw;display:grid;grid-template-columns:2fr 2fr 2fr .3fr}.sampleResult ul li img{width:20px;height:15px}.sampleResult ul li button{padding:.1vw}

.info{min-height:10vw;padding:20px 40px;border:1px solid #BFC4CB}

.dark{position:fixed;top:0;bottom:0;left:0;right:0;background:#1e1e1e;opacity:.6;z-index:7;display:none}.showDark{display:block}

.modal{width:18.5vw;height:25vw;background:#B9D6F3;border:1px solid #ccc;opacity:1;z-index:8;position:absolute;margin:auto;top:0;left:0;right:0;bottom:0;border-radius:5px;display:none}.modalHead{padding:.7vw;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.modalHead ul{margin:0;padding:0;list-style:none}.modalHead ul :nth-child(1){margin-right:.5vw}.modalHead ul li{display:inline-block;cursor:pointer}.modalHead ul li a{text-decoration:none;color:#223046;font-size:1vw}.modalHead i{font-size:1vw}.modalShow{display:block}

.modalBtn{margin:3vw auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}

.modalInput{margin:1vw auto;padding:.5vw;display:grid;grid-template-columns:1fr}.modalInput span{color:#223046;font-family:'Lato', sans-serif;font-size:.7vw;line-height:1.5vw}.modalInput input{height:1.3vw}.modalBtn{margin:3vw auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}

.modalInput{margin:1vw auto;padding:.5vw;display:grid;grid-template-columns:1fr}.modalInput label{color:#223046;font-family:'Lato', sans-serif;font-size:.7vw;line-height:1.5vw}.modalInput input{height:1.3vw}.modalInput span{color:red;font-size:14px;font-weight:bold}.inputInvalid label{color:red}

